* {
  box-sizing: border-box;
}

html {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  line-height: 23px;
}

body {
  margin: 0;
}

.people {
  grid-template-rows: 1fr 1fr;
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  align-items: center;
  gap: 30px 40px;
  padding: 30px 35px;
  display: grid;
}

@media (width <= 999px) {
  .people {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}

img {
  border-radius: 200px;
  width: 200px;
  height: 200px;
  position: relative;
}

.rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 200px;
  justify-content: center;
  align-items: center;
  font-size: 19px;
  font-weight: bold;
  display: flex;
  position: absolute;
}

.details {
  color: #2f4f4f;
  text-align: center;
  margin-top: 3px;
  font-weight: bold;
}

.extra {
  position: relative;
  overflow: hidden;
}
